.movieView {
  background-size: cover;
  background-position: center;
  height: 80vh;
}
.movieTitle {
  font-size: 3em;
  color: #262626;
  margin: 0;
  padding: 20px;
}
.movieOverview {
  font-size: .75em;
  padding: 20px 100px;
}
.movieInfo {
  background-color: rgba(184, 184, 184, 0.85);
  height: 100%;
  padding: 0;
}
@media screen and (max-width: 540px) {
  .movieView {
    height: 85vh;
  }
}
